import React, { useState } from 'react';
import { string } from 'prop-types';
import {
  Box,
  Button,
  Center,
  FormControl,
  FormErrorMessage,
  FormHelperText,
  FormLabel,
  Input,
  Select,
  Text,
  useToast,
  Heading,
} from '@chakra-ui/react';

import ImgBorder from '@/assets/border/rsvp.png';
import WithAnimation from '@components/Common/WithAnimation';
import Image from '@/components/Common/LazyImage';

import useMutation from '@/hooks/useMutation';
import useGuestName from '@/hooks/useGuestName';
import usePartner from '@/hooks/usePartner';

import { BG_RSVP } from '@/constants/assets';
import { API_HOSTNAME, API_RSVP_TABLE } from '@/constants';
import { ENABLE_DIFFERENT_GUEST_PARTNER_RSVP } from '@/constants/feature-flags';
import { Title, txtForm, txtTitle } from './locales';

const TYPE = {
  YES: 'YA',
  NO: 'TIDAK',
};

const ERROR_TYPE = {
  name: undefined,
  email: undefined,
  phone: undefined,
  address: undefined,
};

const INPUT_COMMON_PROPS = {
  isRequired: true,
  size: 'sm',
  type: 'text',
  variant: 'outline',
  colorScheme: 'blackAlpha',
  backgroundColor: 'bgPrimary',
  color: 'mainColorText',
  fontSize: 'md',
  borderRadius: '0',
  fontFamily: 'serif',
  _placeholder: { color: 'mainColorText' },
  _focus: { borderColor: 'mainColorText' },
};

const SELECT_COMMON_PROPS = {
  borderRadius: '0',
  size: 'sm',
  variant: 'outline',
  backgroundColor: 'bgPrimary',
  color: 'mainColorText',
  fontFamily: 'serif',
  _focus: { borderColor: 'mainColorText' },
};

const FORM_LABEL_PROPS = {
  ontSize: 'sm',
  color: 'secondaryColorText',
  margin: '8px 0',
  fontFamily: 'serif',
};

/**
 * function to render RSVP component
 * @returns {JSX.Element}
 * @author idindrakusuma
 */
function RSVPSection({ lang, ...rest }) {
  const toast = useToast();
  const guestName = useGuestName();
  const partner = usePartner();
  const finalName = guestName.toLowerCase() === 'guest' ? '' : guestName;

  const [onSentConfirmation, loading] = useMutation(API_HOSTNAME, API_RSVP_TABLE, 'insert');

  const [name, setName] = useState(() => finalName);
  // const [phone, setPhone] = useState('');
  const [address, setAddress] = useState('-');
  const [attended, setAttended] = useState(TYPE.YES);
  const [bringPartner, setBringPartner] = useState(TYPE.NO);
  const [errorType, setErrorType] = useState(ERROR_TYPE);

  /**
   * function to set state
   * @param {object} e - html event value
   * @param {function} setState
   * @param {string} typeState
   * @returns
   */
  const onSetState = (e, setState, typeState) => {
    const value = e.target.value;
    setState(value);

    if (typeState === 'email') {
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(value)) {
        setErrorType({ ...errorType, email: txtForm.invalidEmail[lang] });
        return;
      }
    }

    // if (typeState === 'phone') {
    //   if (!/\d+/.test(value) || value.length > 13) {
    //     setErrorType({ ...errorType, phone: txtForm.invalidPhone[lang] });
    //     return;
    //   }
    // }

    setErrorType(ERROR_TYPE);
  };

  /**
   * function to submit to BE with check the form value first
   */
  const onSubmitForm = async () => {
    if (errorType.name) return;

    if (!name) {
      setErrorType({
        name: !name ? txtForm.required[lang] : '',
        // phone: !phone ? txtForm.required[lang] : '',
        address: !address ? txtForm.required[lang] : '',
      });

      return;
    }

    const query = `nama=${encodeURIComponent(
      name,
    )}&hadir=${attended}&jumlah_tamu=${bringPartner}&alamat=${encodeURIComponent(address)}`;
    const result = await onSentConfirmation(query);

    if (result.success) {
      toast({
        title: txtForm.success[lang],
        description: txtForm.msgSuccess[lang],
        status: 'success',
        duration: 5000,
        isClosable: true,
      });

      setName('');
      // setPhone('');
      setAddress('');
    } else {
      toast({
        title: 'Oops!',
        description: txtForm.msgError[lang],
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }
  };

  return (
    <Box padding="62px 24px 42px 24px" bgColor="bgPrimary">
      <Box bgColor="mainColor" borderRadius="2xl" borderTopRadius="0" boxShadow="xl" {...rest}>
        <WithAnimation>
          <Image src={BG_RSVP} maxWidth="100%" borderRadius="24px" transform="translateY(-24px)" />
          <Heading
            fontFamily="cursive"
            textAlign="center"
            fontWeight="normal"
            fontSize="2xl"
            color="secondaryColorText"
            textShadow="1px 1px 1px #7A7A7A"
            marginTop="-130px"
            marginBottom="24px"
          >
            The perfect marriage is when two inperfect couple refuse to give up to one another
          </Heading>
        </WithAnimation>
        <Box height="24px" marginTop="-5px" />
        <Box padding="8px" marginTop="-5px">
          <WithAnimation>
            <Box
              bgImage={`url(${ImgBorder})`}
              bgRepeat="no-repeat"
              bgSize="100% 100%"
              padding="24px"
              width="100%"
            >
              {/* Title & Desc Section */}
              <Box textAlign="center" color="secondaryColorText">
                <Text fontSize="4xl" fontFamily="cursive" marginTop="16px">
                  {Title[lang]}
                </Text>
                <Text fontSize="md" fontFamily="serif" margin="16px 0 24px 0">
                  {txtTitle[lang]}
                </Text>
              </Box>

              {/* Form Sections - Name */}
              <FormControl margin="8px 0" isInvalid={errorType.name}>
                <FormLabel {...FORM_LABEL_PROPS}>{txtForm.name[lang]}:</FormLabel>
                <Input
                  {...INPUT_COMMON_PROPS}
                  placeholder="..."
                  value={name}
                  onChange={(e) => onSetState(e, setName)}
                />
                <FormErrorMessage color="whiteAlpha.600" marginTop="4px">
                  {errorType.name}
                </FormErrorMessage>
              </FormControl>
              {/* Form Sections - Phone Number */}
              {/* {
                <FormControl margin="16px 0" isInvalid={errorType.phone}>
                  <FormLabel {...FORM_LABEL_PROPS}>{txtForm.hp[lang]}:</FormLabel>
                  <Input
                    {...INPUT_COMMON_PROPS}
                    placeholder="..."
                    value={phone}
                    onChange={(e) => onSetState(e, setPhone, 'phone')}
                  />
                  <FormErrorMessage color="whiteAlpha.800" marginTop="4px">
                    {errorType.phone}
                  </FormErrorMessage>
                </FormControl>
              } */}
              {/* Form Sections - Address */}
              {false && (
                <FormControl margin="16px 0" isInvalid={errorType.address}>
                  <FormLabel {...FORM_LABEL_PROPS}>{txtForm.address[lang]}:</FormLabel>
                  <Input
                    {...INPUT_COMMON_PROPS}
                    height="40px"
                    placeholder="..."
                    value={address}
                    onChange={(e) => onSetState(e, setAddress)}
                  />
                  <FormErrorMessage marginTop="4px">{errorType.address}</FormErrorMessage>
                </FormControl>
              )}
              {/* Form Sections - Attendance */}
              <FormControl margin="8px 0">
                <FormLabel {...FORM_LABEL_PROPS}>{txtForm.willYoutAttend[lang]}</FormLabel>
                <Select
                  {...SELECT_COMMON_PROPS}
                  value={attended}
                  onChange={(e) => onSetState(e, setAttended)}
                >
                  <option value={TYPE.YES} color="white">
                    {txtForm.willAttend[lang]}
                  </option>
                  <option value={TYPE.NO}>{txtForm.noAttend[lang]}</option>
                </Select>
              </FormControl>
              {/* Form Sections - Partner */}
              {attended === TYPE.YES && (
                <>
                  <FormControl>
                    <FormLabel {...FORM_LABEL_PROPS}>{txtForm.willYouBringPartner[lang]}</FormLabel>
                    <Select
                      {...SELECT_COMMON_PROPS}
                      value={bringPartner}
                      onChange={(e) => onSetState(e, setBringPartner)}
                    >
                      {ENABLE_DIFFERENT_GUEST_PARTNER_RSVP ? (
                        <>
                          {!partner && (
                            <>
                              <option value="1 Partner" style={{ color: 'black' }}>
                                {txtForm.withPartner[lang]}
                              </option>
                              <option value="2 Partner" style={{ color: 'black' }}>
                                Ya, saya akan membawa 2 partner
                              </option>
                              <option value={TYPE.NO} style={{ color: 'black' }}>
                                {txtForm.noPartner[lang]}
                              </option>
                            </>
                          )}
                          {/* {partner === 1 && (
                            <>
                              <option value="1 Partner" style={{ color: 'black' }}>
                                {txtForm.withPartner[lang]}
                              </option>
                              <option value={TYPE.NO} style={{ color: 'black' }}>
                                {txtForm.noPartner[lang]}
                              </option>
                            </>
                          )}
                          {partner === 2 && (
                            <>
                              <option value="1 Partner" style={{ color: 'black' }}>
                                {txtForm.withPartner[lang]}
                              </option>
                              <option value="2 Partner" style={{ color: 'black' }}>
                                Ya, saya akan membawa 2 partner
                              </option>
                              <option value={TYPE.NO} style={{ color: 'black' }}>
                                {txtForm.noPartner[lang]}
                              </option>
                            </>
                          )} */}
                        </>
                      ) : (
                        <>
                          <option value={TYPE.YES}>{txtForm.withPartner[lang]}</option>
                          <option value={TYPE.NO}>{txtForm.noAttend[lang]}</option>
                        </>
                      )}
                    </Select>
                    {bringPartner === TYPE.YES && false && (
                      <FormHelperText color="mainColorText" fontSize="10px" fontStyle="italic">
                        *) Berdasarkan kondisi saat ini, satu tamu hanya boleh membawa 1 orang
                        partner atau pasangan dalam satu undangan. Terima kasih atas pengertiannya.
                      </FormHelperText>
                    )}
                  </FormControl>
                </>
              )}
              <Center>
                <Button
                  color="mainColorText"
                  fontWeight="light"
                  isLoading={loading}
                  marginTop="24px"
                  size="sm"
                  type="button"
                  bgColor="bgPrimary"
                  onClick={onSubmitForm}
                >
                  {txtForm.submit[lang]}
                </Button>
              </Center>
            </Box>
          </WithAnimation>
        </Box>
      </Box>
    </Box>
  );
}
RSVPSection.propTypes = {
  lang: string,
};

RSVPSection.defaultProps = {
  lang: 'id',
};

export default React.memo(RSVPSection);
