import React from 'react';
import { bool, func } from 'prop-types';

import {
  Text,
  Box,
  Center,
  Button,
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerBody,
  DrawerHeader,
  DrawerCloseButton,
} from '@chakra-ui/react';

import txtWording from './locales';
import { string } from 'prop-types';

function ModalAddress({ onClose, visible, onCopyText, lang }) {
  const address = 'Graha Estetika, Jl. Serasi B1-B2, RT/RW 001/008, Pedalangan, Banyumanik, Semarang';

  const renderBank = () => {
    return (
      <Box padding="16px 0">
        <Text textAlign="center" fontSize="lg" fontFamily="serif" color="mainColor" marginTop="0">
          Raina Rusdianto
          <br />
        </Text>
        <Text textAlign="center" color="black" fontSize="sm" fontFamily="serif">
          {address}
        </Text>
        <Center>
          <Button
            size="xs"
            fontWeight="normal"
            colorScheme="blackAlpha"
            bgColor="mainColor"
            marginTop="16px"
            onClick={() => onCopyText(address, txtWording.successCopyAddress[lang])}
          >
            {txtWording.modalCopy[lang]}
          </Button>
        </Center>
      </Box>
    );
  };

  return (
    <Drawer placement="bottom" onClose={onClose} isOpen={visible}>
      <DrawerOverlay />
      <DrawerContent>
        <DrawerCloseButton marginTop="12px" />
        <DrawerHeader borderBottomWidth="1px" fontSize="2xl" color="mainColor" fontFamily="cursive">
          {txtWording.sendGift[lang]}
        </DrawerHeader>
        <DrawerBody>
          <Text align="center" fontFamily="serif" fontSize="sm" margin="16px 0" color="black">
            {txtWording.modalDescAddress[lang]}
          </Text>
          {renderBank()}
        </DrawerBody>
      </DrawerContent>
    </Drawer>
  );
}

ModalAddress.propTypes = {
  lang: string,
  visible: bool.isRequired,
  onClose: func.isRequired,
  onCopyText: func.isRequired,
};

ModalAddress.defaultProps = {
  lang: 'id',
};

export default ModalAddress;