import React from 'react';
import { bool, func, string } from 'prop-types';

import {
  Text,
  Box,
  Center,
  Button,
  Drawer,
  DrawerOverlay,
  DrawerContent,
  DrawerBody,
  DrawerCloseButton,
  DrawerFooter,
  DrawerHeader,
  Heading,
  Image,
} from '@chakra-ui/react';

import QRBank from '@/images/bca.jpg';
import BANK from '@invitato/helpers/dist/constants/banks';
import txtWording from './locales';

function ModalBank({ onClose, visible, onCopyText, lang }) {
  const renderBank = (bank, userName, bankNumber) => {
    return (
      <Box padding="16px 0" fontFamily="serif" textAlign="center">
        <Center>
          <Image src={bank.logo} marginBottom="8px" maxWidth="80px" />
        </Center>
        <Text color="mainColor" marginTop="0" fontWeight="bold">
          {bankNumber}
        </Text>
        <Text color="black">{userName}</Text>
        <Center>
          <Button
            size="xs"
            fontWeight="normal"
            colorScheme="blackAlpha"
            bgColor="mainColor"
            marginTop="16px"
            onClick={() => onCopyText(bankNumber)}
          >
            {txtWording.modalCopy[lang]}
          </Button>
        </Center>
      </Box>
    );
  };

  return (
    <Drawer size="full" onClose={onClose} isOpen={visible}>
      <DrawerOverlay />
      <DrawerContent>
        <DrawerCloseButton marginTop="12px" />
        <DrawerHeader />
        <DrawerBody>
          <Heading
            textAlign="center"
            fontWeight="normal"
            fontSize="3xl"
            color="mainColor"
            fontFamily="cursive"
            marginTop="24px"
          >
            Bank Transfer
          </Heading>
          <Text align="center" fontFamily="normal" fontSize="sm" margin="16px 0" color="black">
            {txtWording.modalDesc[lang]}
          </Text>
          {false && (
            <Center>
              <Image
                src={QRBank}
                borderRadius="16px"
                marginTop="8px"
                maxWidth="200px"
                boxShadow="md"
                padding="8px"
              />
            </Center>
          )}
          {renderBank(BANK.bca, 'Feliciana Regina Putri', '8070607426')}
          {renderBank(BANK.jenius, 'Feliciana Regina Putri', '90012587664')}
        </DrawerBody>
        {false && (
          <DrawerFooter>
            <Button colorScheme="orange" fontWeight="normal" size="sm" mr={3} onClick={onClose}>
              Tutup
            </Button>
            {false && (
              <Button colorScheme="orange" size="sm" onClick={() => onCopyText('8648158740')}>
                Salin Nomor Rekening
              </Button>
            )}
          </DrawerFooter>
        )}
      </DrawerContent>
    </Drawer>
  );
}

ModalBank.propTypes = {
  lang: string,
  visible: bool.isRequired,
  onClose: func.isRequired,
  onCopyText: func.isRequired,
};

ModalBank.defaultProps = {
  lang: 'id',
};

export default ModalBank;
